import React, { useState, useRef, useCallback, useEffect } from 'react';
import { getChatGPTResponse, iChatMessage, initAi } from '../tools/api';
import styled, { css } from 'styled-components';
import ReactMarkdown from 'react-markdown';
import { Loading } from './loading';
import { Markdown } from './markdown';
import { copyToClipboard } from './misc';
import { useAtom } from 'jotai';
import { chatHistoryAtom, iChatHistory, savedChatHistoryListAtom } from '../states/atoms';
import { Verify } from 'crypto';
import { Version } from './version';


const StyledChatApp = styled.div`
  margin: 0 auto;
  padding-top: 10px;
  box-sizing: border-box;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: left;
  overflow: hidden;
  max-width: 900px;
`;

const whiteBg = ['#f1f1f1', '#f5f5f5'];
const myBgColors = ['#d2d2de', '#d8d8e2'];
const myBg = (colors: string[]) => css<any>`
  background: repeating-linear-gradient(
    -22deg,
    ${colors[0]},
    ${colors[0]} 4px,
    ${colors[1]} 4px,
    ${colors[1]} 8px
  );
`;

const StyledChatHistory = styled.div`
  ${myBg(myBgColors)}
  flex-grow: 2;
  margin: 0 10px 10px;
  border-radius: 4px;
  border: 1px solid #0003;
  box-shadow: inset 0 1px 8px #0003;
  overflow: hidden;
  /* 分成内外两层，防止圆角处显示溢出 */
  .inner {
    overflow: auto;
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    padding: 12px 12px 8px 12px;
    display: flex;
    flex-direction: column;
  }
`;

const StyledChatList = styled(StyledChatHistory)`
  background: #ebebe8;
`;

const PlaceHolder = styled.div`
  flex-grow: 1;
  flex-shrink: 9;
`

const StyledChatListBubble = styled.div<any>`
  ${p => p.chatType === 'current' ? myBg(myBgColors) : myBg(whiteBg)}
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0;
  border-radius: 4px;
  margin: 0 0 10px;
  border: 1px solid #0003;
  color: #000a;
  font-size: 12px;
  box-shadow: 0 1px 8px #0003;
  .btns {
    padding: 10px 0 10px 10px;
    flex-shrink: 0;
    font-weight: bold;
    font-size: 14px;
    button.delete-btn {
      padding: 0;
      background: none;
      border: none;
      cursor: pointer;
      font-size: 14px;
      color: #0006;
    }
  }
  .content {
    cursor: pointer;
    padding: 10px;
    .number {
      margin-right: 4px;
      color: #000a;
    }
  }
`;

const StyledBubble = styled.div<any>`
  padding: 10px;
  border-radius: 4px;
  background-color: ${(p) => (p.role === 'user' ? '#fafafd' : '#fefef9')};
  margin: 0 0 10px;
  border: 1px solid #0003;
  .top-bar {
    display: flex;
    flex-direction: row;
    .name {
      font-weight: bold;
      font-size: 14px;
      color: #0006;
    }
    .plase-holder {
      flex-grow: 1;
    }
    .tool-btns {
      display: flex;
      flex-direction: row;
      button {
        margin-left: 16px;
        padding: 0;
        background: none;
        border: none;
        cursor: pointer;
        font-size: 14px;
        color: #0006;
        &:hover {
          color: #0009;
        }
      }
    }
  }
`;

const StyledChatInput = styled.div`
  flex-shrink: 0;
  margin: 0 10px 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  /* width: 100%; */
  overflow: hidden;
  height: 74px;
  .textarea-wrapper {
    box-sizing: border-box;
    height: 100%;
    flex-grow: 1;
    margin: 0;
    padding: 0;
    border: 1px solid #0003;
    border-radius: 4px;
    overflow: hidden;
    textarea {
      box-sizing: border-box;
      height: 100%;
      width: 100%;
      margin: 0;
      padding: 8px;
      border: none;
      resize: none;
      overflow-y: auto;
      &:focus {
        outline: none;
      }
    }
  }
`;

const StyledKeyInput = styled.div<any>`
  ${p => p.entered ? 'opacity: 0.4;' : ''}
  display: flex;
  flex-direction: row;
  margin: 0 10px 10px;
  padding-left: 2px;
  align-items: center;
  color: #0008;
  input {
    font-family: monospace;
    margin-left: 10px;
    flex-grow: 1;
    height: 32px;
    box-sizing: border-box;
    border: 1px solid #0003;
    border-radius: 4px;
    padding-left: 8px;
    &:focus {
      outline: none;
    }
  }
`;

export const StyledPromptInput = styled.div`
  display: flex;
  flex-direction: row;
  margin: 10px 10px 0;
  textarea {
    height: 5em;
    margin-left: 10px;
    flex-grow: 1;
  }
`;

const StyledButtonList = styled.div<any>`
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  overflow: auto hidden;
  margin: 0px 0px 10px 10px;
  button {
    ${(p) => p.fillwidth ? 'flex-grow: 1;' : ''}
  }
`;

const StyledUseInfo = styled.div`
  font-size: 14px;
  color: #0005;
  text-align: center;
  margin-bottom: 16px;
`;

const makeUserMessage = (text: string): iChatMessage => {
  return {
    role: 'user',
    content: text,
  };
};

const StyledButton = styled.button<any>`
  margin-right: 10px;
  margin-left: 0;
  height: 32px;
  box-sizing: border-box;
  color: #000b;
  background-color: #e5e5e5;
  border: 1px solid #0003;
  border-radius: 4px;
  cursor: pointer;
  &.red {
    background-color: #fdd;
  }
  &.h-white {
    ${myBg(whiteBg)}
  }
  &.h-blue {
    ${myBg(myBgColors)}
  }
  &.blue {
    background-color: #ddf;
  }
  &:disabled {
    opacity: 0.5;
  }
  /* 不影响移动端的 hover 样式 */
  @media (hover: hover) {
    &:hover {
      filter: brightness(0.9);
      &:disabled {
        filter: none;
      }
    }
  }
`;

const StyledChatButtonList = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  button {
    margin: 0 0 0 10px;
    width: 80px;
  }
`;

const summaryLength = 140;
const getMassagesSummary = (messages: iChatMessage[]) => {
  let summary = '';
  for (let i = 0; i < messages.length; i++) {
    const message = messages[i];
    if (message.role === 'user') {
      summary += message.content;
    }
    if (summary.length >= summaryLength) {
      break;
    }
  }
  if (summary === '') {
    return '空对话';
  } else {
    return summary.slice(0, summaryLength) + (summary.length > summaryLength ? ' ...' : '');
  }
};

export const ChatGPT: React.FC = () => {

  const [chatHistory, setChatHistory] = useAtom(chatHistoryAtom);
  const [chatList, setChatList] = useAtom(savedChatHistoryListAtom);

  const [chatText, setChatText] = useState<string>('');
  const [apiKey, setApiKey] = useState<string>('');
  const [showKeyInput, setShowKeyInput] = useState<boolean>(true);
  const [ai, setAi] = useState<any>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [chatListMode, setChatListMode] = useState<boolean>(false);

  const chatInputRef = useRef<any>(null);
  const apiKeyInputRef = useRef<any>(null);

  const toggleChatListMode = () => {
    setChatListMode(!chatListMode);
  }

  const makeNewChatHistory = () => {
    const l = 60
    return {
      name: chatHistory[0].content.slice(0, l),
      messages: chatHistory,
    }
  };

  const newChat = () => {
    setChatListMode(false);
    if (chatHistory.length === 0) return;
    setChatList((list) => [makeNewChatHistory(), ...list]);
    setChatHistory([]);
  };

  // 保存当前聊天记录并打开聊天记录
  const openChat = (index: number) => {
    // 读取聊天记录
    const chat = chatList[index];
    setChatHistory(chat.messages);

    // 保存当前聊天记录
    if (chatHistory.length !== 0) {
      const newChatList = [...chatList];
      newChatList.splice(index, 1);
      newChatList.unshift(makeNewChatHistory());
      setChatList(newChatList)
    }

    // 关闭聊天记录列表
    setChatListMode(false);
  };

  const deleteChatHistory = (index: number) => {
    const newChatList = [...chatList];
    newChatList.splice(index, 1);
    setChatList(newChatList);
  };

  // 更新 chatText
  const handleChange = (event: any) => {
    setChatText(event.target.value);
    setShowKeyInput(false);
  };

  // 添加历史记录
  const addHistory = (message: iChatMessage) => {
    setChatHistory((history) => [...history, message]);
  };

  const putChatContent = (content: string) => {
    if (!chatInputRef.current) return;
    chatInputRef.current.value = content;
    setChatText(content);
  };

  // 清空输入框
  const clearChatInput = () => {
    putChatContent('');
  };

  const handleSubmit = useCallback(async () => {
    if (!ai) return;

    setLoading(true);

    try {
      const responseMessage = await getChatGPTResponse(ai)(
        chatText,
        chatHistory
      );
      addHistory(responseMessage);
    } catch (error) {
      console.error(error);
    }

    setLoading(false);
  }, [ai, chatHistory, chatText]);

  const handleSend = () => {
    addHistory(makeUserMessage(chatText));
    clearChatInput();
  };

  const handleApiKeyChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const _apiKey = event.target.value;
    setApiKey(_apiKey);
    const _ai = initAi(_apiKey);
    setAi(_ai);
  };

  const removeFromHistory = (index: number) => {
    setChatHistory((history) => {
      const newHistory = [...history];
      newHistory.splice(index, 1);
      return newHistory;
    });
  };

  const chatInputPlaceholder = !apiKey
    ? '请先输入 Api key'
    : '在这里输入提问内容，发送，然后请求回复';

  return (
    <StyledChatApp>
      {
        (apiKey.length < 1 || showKeyInput) && <StyledKeyInput>
          API key
          <input ref={apiKeyInputRef} type="password" value={apiKey} onChange={handleApiKeyChange} />
        </StyledKeyInput>
      }

      <StyledButtonList fillwidth>
        <StyledButton
          disabled={showKeyInput && apiKey.length < 1}
          onClick={() => {
            setShowKeyInput(!showKeyInput);
          }}>API key</StyledButton>
        {
          !chatListMode ?
            <StyledButton
              className='h-white'
              onClick={() => {
                setChatListMode(true);
              }}
            >历史对话</StyledButton>
            :
            <StyledButton
              className='h-blue'
              onClick={() => {
                setChatListMode(false);
              }}
            >当前对话</StyledButton>

        }
        <StyledButton
          disabled={chatHistory.length === 0}
          className='h-white'
          onClick={() => newChat()}
        >新对话</StyledButton>
      </StyledButtonList>

      {
        chatListMode ?
          <StyledChatList>
            <div className='inner'>
              <div key={'current'} onClick={
                () => {
                  setChatListMode(false);
                }
              }>
                <StyledChatListBubble chatType='current'>
                  <div className='btns'>
                    当前
                  </div>
                  <div className='content'>
                    <span className='number'>
                      ({chatHistory.length})
                    </span>
                    {getMassagesSummary(chatHistory)}
                  </div>
                </StyledChatListBubble>
              </div>
              {chatList.map((chat, index) => (
                <div key={index}>
                  <StyledChatListBubble>
                    <div className='btns'>
                      <button
                        className='delete-btn'
                        onClick={() => {
                          deleteChatHistory(index)
                        }}>删除</button>
                    </div>
                    <div className='content' onClick={() => {
                      openChat(index);
                    }}>
                      <span className='number'>
                        ({chat.messages.length})
                      </span>
                      {getMassagesSummary(chat.messages)}
                    </div>
                  </StyledChatListBubble>
                </div>
              ))}
              <PlaceHolder />
              <Version />
            </div>
          </StyledChatList>
          : <StyledChatHistory>
            <div className='inner'>
              {
                // chatHistory.length === 0 &&
                <StyledUseInfo>
                  「发送」消息之后要记得「生成回复」，可以「发送」多条信息后再「生成回复」。适当删除不需要的消息可以更快地生成回复。
                </StyledUseInfo>
              }
              {chatHistory.map((message, index) => (
                <div key={index}>
                  <StyledBubble role={message.role}>
                    <div className="top-bar">
                      <div className="name">
                        {message.role === 'user' ? '我' : '机器人'}
                      </div>
                      <div className="plase-holder" />
                      <div className="tool-btns">
                        <button
                          onClick={() => {
                            copyToClipboard(message.content);
                          }}
                        >
                          复制
                        </button>
                        <button
                          onClick={() => {
                            removeFromHistory(index);
                          }}
                        >
                          删除
                        </button>
                      </div>
                    </div>
                    <Markdown>{message.content}</Markdown>
                  </StyledBubble>
                </div>
              ))}
              {
                loading &&
                <StyledBubble role={'assistant'}>
                  <div className="top-bar">
                    <div className="name">
                      {'机器人'}
                    </div>
                  </div>
                  <Loading />
                </StyledBubble>
              }
              <PlaceHolder />
              <Version />
            </div>
          </StyledChatHistory>
      }

      {!chatListMode && <>
        <StyledChatInput>
          <div className='textarea-wrapper'>
            <textarea
              ref={chatInputRef}
              onChange={handleChange}
              placeholder={chatInputPlaceholder}
              disabled={!apiKey || !ai}
            />
          </div>
          <StyledChatButtonList>
            <StyledButton
              className='blue'
              onClick={handleSend}
              disabled={!ai || loading || chatText.length < 1}
            >
              发送
            </StyledButton>
            <StyledButton
              onClick={handleSubmit}
              disabled={!ai || loading || chatHistory.length < 1 || chatHistory[chatHistory.length - 1].role !== 'user'}
            >
              生成回复
            </StyledButton>
          </StyledChatButtonList>
        </StyledChatInput>

        <StyledButtonList>
          <StyledButton
            disabled={chatText.length < 1}
            className="red"
            onClick={() => putChatContent('')}>
            清空输入
          </StyledButton>
          <StyledButton
            onClick={() => {
              const text = '我想向朋友请教同样的问题，请将我的问题总结成几个简洁的问句，问句之间尽量不要有重复的内容';
              addHistory(makeUserMessage(text));
            }
            }
          >
            总结提问
          </StyledButton>
          <StyledButton
            onClick={() => {
              const text = '请作为web开发专家基于我的开发习惯辅助我进行开发，我的习惯如下:' +
                '\n- 使用React' +
                '\n- 使用TypeScript' +
                '\n- 尽量使用箭头函数、纯函数与函数组件' +
                '\n- 使用styled-components处理样式' +
                '\n- 尽量将代码拆分成小的函数' +
                '\n- 不使用export default' +
                '\n\n要求如下:' +
                '\n- 回复中的所有的代码都包裹在代码块中 ' +
                '\n\n接下来我将提出一些开发相关的问题或任务';
              addHistory(makeUserMessage(text));
            }}
          >
            React 开发咨询
          </StyledButton>
          <StyledButton
            onClick={() => {
              const text = '请把我写的句子翻译成表情符号。我会写句子，你用表情符号表达它。我只是想让你用表情符号来表达它。除了表情符号，我不希望你回复任何内容。接下来我将开始发送句子。';
              addHistory(makeUserMessage(text));
            }
            }
          >
            表情翻译
          </StyledButton>
        </StyledButtonList>

        {/* <Timer /> */}
      </>}

    </StyledChatApp>
  );
};
