import React, { useState } from 'react';
import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { atomDark } from 'react-syntax-highlighter/dist/esm/styles/prism';
import { copyToClipboard } from './misc';
import styled from 'styled-components';

const StyledInlineCode = styled.code`
  background-color: #0002;
  padding: 2px 4px;
  border-radius: 4px;
  font-size: 14px;
  color: #000b;
`;

const StyledCopyButton = styled.button`
  position: absolute;
  top: 4px;
  right: 4px;
  background-color: #fff5;
  padding: 2px 4px;
  border-radius: 4px;
  font-size: 12px;
  color: #000b;
  cursor: pointer;
  border: none;
  &:hover {
    background-color: #fff8;
  }
`;

const SyntaxedCode = ({ node, inline, className, children, ...props }: any) => {
  const [copied, setCopied] = useState(false);
  const codeBlockRef = React.useRef<HTMLDivElement>(null);

  const handleCopy = () => {
    const text = String(children);
    if (text.length > 0) {
      copyToClipboard(text);
      setCopied(true);
      setTimeout(() => {
        setCopied(false);
      }, 1000);
    }
  };

  const match = /language-(\w+)/.exec(className || '');
  return !inline && match ? (
    <div ref={codeBlockRef} style={{ position: 'relative' }}>
      <SyntaxHighlighter
        children={String(children).replace(/\n$/, '')}
        style={atomDark as any}
        language={match?.[1] || 'text'}
        PreTag="div"
        {...props}
      />
      <StyledCopyButton className="copy-button" onClick={handleCopy}>
        {copied ? '复制成功!' : '复制'}
      </StyledCopyButton>
    </div>
  ) : (
    <StyledInlineCode className={className} {...props}>
      {children}
    </StyledInlineCode>
  );
};

export const Markdown: React.FC<{ children: any }> = ({ children }): any => {


  return (
    <ReactMarkdown components={{ code: SyntaxedCode }}>
      {children}
    </ReactMarkdown>
  );
};
