import { atom } from 'jotai';
import { atomWithStorage } from 'jotai/utils';
import { iChatMessage } from '../tools/api';

const storageKeyPrefix = 'CHAT_BOT__';
const getStorageKey = (key: string) => `${storageKeyPrefix}${key}`;

export interface iChatItem {
  selected: boolean;
  message: iChatMessage;
}

// 当前对话历史
export const chatHistoryAtom = atomWithStorage<iChatMessage[]>(getStorageKey('chatHistory'), []);

export interface iChatHistory {
  name: string;
  messages: iChatMessage[];
}

// 保存的对话历史
export const savedChatHistoryListAtom = atomWithStorage<iChatHistory[]>(getStorageKey('savedChatHistoryList'), []);

// api key
export const apiKeyAtom = atom<string | null>(null);
