import { Configuration, OpenAIApi } from 'openai';

const MODEL = 'gpt-3.5-turbo';

export const initAi = (apiKey: string) => {
  const aiConf = new Configuration({
    // organization: 'org-fQEZL5upgtWBzNbrhhcWrtyB',
    apiKey: apiKey,
  });

  const ai = new OpenAIApi(aiConf);
  return ai;
};

export interface iChatMessage {
  role: 'user' | 'assistant';
  content: string;
}

export const getChatGPTResponse =
  (ai: any) =>
  async (input: string, history: iChatMessage[] = []): Promise<iChatMessage> => {
    try {
      const requestData = {
        model: MODEL,
        // prompt: prompt || '',
        messages: history,
      };
      console.log('[AI] 请求 ChatGPT Api', requestData);
      const response = await ai.createChatCompletion(requestData);
      return response?.data?.choices?.[0]?.message || '';
    } catch (error) {
      console.error(error);
      throw new Error('[AI] 请求 ChatGPT Api 失败');
    }
  };
